import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'

export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem
	},
	props:['todolist'],
	data() {
		return {
			isOpened: false,
			options1: [{
				text: '标记为已完成',
				style: {
					backgroundColor: 'rgb(254,156,1)'
				}
			}, {
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
			options2: [{
				text: '撤销完成标记',
				style: {
					backgroundColor: 'rgb(178, 170, 170)'
				}
			}, {
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
			options3: [{
				text: '为啥没完成呢?',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
			options4: [{
				text: '很棒哦！继续努力',
				style: {
					backgroundColor: '#39B54A'
				}
			}],
			subjects: ['数学', '英语', '专业课','政治'],
			learnStyle:['做题', '背单词', '看书', '看视频'],
				
		}
	},
	onReady() {
		this.$nextTick(() => {
			this.isOpened = true
		})
	},
	methods: {
		getDate(){
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
				
			return `${year}-${month}-${day}`;
		},
		bindClick(e) {
			uni.showToast({
				title: `点击了${e.content.text}按钮`,
				icon: 'none'
			})
		},
		setOpened() {
			this.isOpened = !this.isOpened
		},
		change(e) {
			this.isOpened = e
			console.log('返回：', e);
		},
		swipeChange(e) {
			console.log('返回：', e);
		},
		swipeClick(e, index, todoid) {
			let {
				content
			} = e
			if (content.text === '删除') {
				uni.showModal({
					title: '提示',
					content: '是否删除',
					success: (res) => {
						if (res.confirm) {
							this.todolist.splice(index, 1)
							this.$emit("deleteTodoItem",todoid)
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else if (content.text === '标记为已完成') {
				this.todolist[index].isCompleted = 1
				this.$emit("completeTodoItem",todoid)
				uni.showToast({
					title: `哎吆，不错哦！`,
					icon: 'none'
				})
			} else if (content.text === '撤销完成标记'){
				this.todolist[index].isCompleted = 0
				this.$emit("uncompleteTodoItem",todoid)
			}

		}
	}
}
